.form {
  border: 2px solid rgb(54, 54, 54);
  width: 400px;
  display: block;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
}

.label {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.input {
  margin-left: 10px;
}

.addbutton {
  width: 100px;
  height: 20px;
  background-color: #1eb432;
  color: rgb(255, 250, 249);
  border: 1px solid rgba(4, 54, 15, 0.904);
  border-radius: 5px;
  display: block;
  margin: 0 auto;
}
